.blog {
  display: grid;
  grid-template-areas: "nav center content";
  grid-template-rows: 100vh;
  grid-template-columns: auto auto 75vw;
}

.blog-navigation {
  background-color: whitesmoke;
  -webkit-transition: width 0.8s ease-in-out;
  -o-transition: width 0.8s ease-in-out;
  transition: width 0.8s ease-in-out;
  grid-area: nav;
}

.blog-navigation#hide {
  width: 30vw;
}

.blog-navigation#show {
  width: 0vw;
}

.blog-center {
  grid-area: center;
  position: relative;
  display: grid;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: width 0.8s ease-in-out;
  -o-transition: width 0.8s ease-in-out;
  transition: width 0.8s ease-in-out;
}

.blog-center#hide {
  width: 70vw;
}

.blog-center#show {
  width: 35vw;
}

.blog-center-content {
  max-width: 40vw;
  padding: 10%;
  z-index: 1;
  display: grid;
  -ms-flex-align: end;
      align-items: flex-end;
  grid-template-columns: 1fr;
  -webkit-animation: fade 1s;
          animation: fade 1s;
}

.blog-center-content > h1 {
  font-size: 3rem;
  font-weight: bold;
}

.blog-center-content > h1,
.blog-center-content > p {
  color: white;
}

.blog-posts {
  background-color: white;
  width: 75vw;
  height: 100%;
  grid-area: content;
  position: relative;
  z-index: 2;
}

#posts-open {
  background-color: white;
  width: 176px;
  position: absolute;
  left: -176px;
  bottom: 48px;
}

#posts-close {
  background-color: white;
  position: absolute;
  left: -48px;
  bottom: 48px;
}

@-webkit-keyframes fade {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes fade {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}



