.background {
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: grid;
  -ms-flex-align: center;
      align-items: center;
}

.background::after {
  background-color: black;
  opacity: 0.5;
  content: "";
  width: 100%;
  height: 100%;
}



